// Variables
.menu {
  display: flex;
  padding: 0;
  margin: 0;
  list-style: none;

  @media screen and (max-width: 1200px) {
    display: none;
  }

  & .link {
    color: inherit;
    text-decoration: none;
  }

  & .link:hover,
  & .link:focus {
    text-decoration: underline;
  }
}

// Base styles for .menu-toggle
.menu-toggle {
  @extend %flex-center;
  min-height: 40px;
  min-width: 40px;
  margin: 0;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
  border: none;
  border-radius: 50%;
  outline: none;
  align-items: center;

  @media screen and (min-width: 1200px) {
    display: none;
  }

  & .menu__icon {
    stroke: $color-white;
    margin: 0;
    @media screen and (min-width: 768px) {
      margin: 24px;
    }
  }

  &:hover .menu__icon,
  &:focus .menu__icon {
    fill: $color-red-1;
  }
}

// Base styles for .mobile__link--active
.mobile__link--active {
  color: $color-red-1 !important;
}

// Base styles for .menu-container
.menu-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 20px 0 0;
  background-color: $color-mobile;
  z-index: 999;
  transform: translateX(100%);
  transition: transform 250ms ease-in-out;
  display: flex;
  justify-content: center;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;

  &.is-open {
    transform: translateX(0);
  }
}

.mobile-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 26px 0 20px;
}

.mobile__line {
  border: 1px solid $color-mobile-1;
  width: 100%;
  margin: 0;
}

.mobile__menu {
  width: 100%;
}

.mobile__menu-sections {
  display: flex;
  justify-content: center;
}

// Base styles for .mobile-menu
.mobile-menu {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  height: auto;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;

  & .link {
    width: 254px;
    margin-left: 20px;
    color: $color-white;
    text-decoration: none;
    @extend %dmSans-700;
    font-size: 14px;
    line-height: 18.23px;
    height: 42px;
    display: flex;
    align-content: center;
    flex-wrap: wrap;

    &:hover,
    &:focus {
      color: $color-red-1;
    }
  }

  &__current {
    color: $color-red-1;
  }
}

.menu-cotainer__btn-close {
  display: flex;
  justify-content: flex-end;
  width: 209px;
  @media screen and (min-width: 768px) {
    width: 186px;
  }
}

.menu-toggle-2 {
  @extend %flex-center;
  width: 14px;
  height: 14px;
  margin: 0;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
  border: none;
}

.menu__icon-2 {
  stroke: $color-white;
  width: 100%;
  height: 100%;
}
